<template>
  <div>
    <van-nav-bar
        title="收货地址"
        left-arrow
        @click-left="$router.go(-1)"
      />
    <div class="AddressHead">
        <div class="headLeft" @click="changeButton = !changeButton">管理</div>
        <div class="headRight" @click="addAddress">新增地址</div>
    </div>

    <div class="AddressBody" v-for="item in AddressList" :key="item.address_id">
        <div class="BodyInfo">
            <span class="name">{{item.name}} <span class="mobile">{{item.phone}}</span></span>
            <span class="address">{{item.region.province + item.region.city +item.region.region + item.detail}}</span>
            <van-checkbox :value="defId === item.address_id" icon-size="18px" class="bodyChecked" @click="setDefAddress(item.address_id)">默认地址</van-checkbox>
        </div>
        <van-icon name="edit" class="BodyRight" size="20px" @click="handleChange(item.address_id)" v-if="changeButton"/>
        <button v-else class="BodyRightButton" @click="removeAddress(item.address_id)">删除</button>
    </div>
                        <!-- 修改地址弹窗 -->
    <van-dialog v-model="editShow" title="修改地址" show-cancel-button @confirm="changeAddress">
        <form class="editForm">
            <div class="NewProvince">
                <span>请输入省份</span>
                <select class="NewProvince" v-model="NewAddress.province">
                <option v-for="item in AllProduct" :key="item.id" :value="item.id">
                    {{ item.name }}
                </option>
                </select>
            </div>
            <div  class="NewCity">
                <span>请输入市</span>
                <select class="NewCity" v-model="NewAddress.city" @click="handleCity">
                <option v-for="item in AllCity" :key="item.id" :value="item.id">
                    {{ item.name }}
                    </option>
                </select>
            </div>
            <div  class="NewRegion">
                <span>请输入区</span>
                <select class="NewRegion" v-model="NewAddress.region" @click="handleRegion">
                <option v-for="item in AllRegion" :key="item.id" :value="item.id">
                    {{ item.name }}
                    </option>
                </select>
            </div>
            <input class="NewDetail" placeholder="请输入详细收货地址" v-model="NewDetail">
            <input class="NewName" placeholder="请输入收货人姓名" v-model="NewName">
            <input class="NewPhone" placeholder="请输入手机号" v-model="NewPhone">
        </form>
</van-dialog>
            <!-- 添加地址弹窗 -->
<van-dialog v-model="addShow" title="添加地址" show-cancel-button @confirm="addAddressDialog">
        <form class="editForm">
            <div class="NewProvince">
                <span>请输入省份</span>
                <select class="NewProvince" v-model="NewAddress.province">
                <option v-for="item in AllProduct" :key="item.id" :value="item.id">
                    {{ item.name }}
                </option>
                </select>
            </div>
            <div  class="NewCity">
                <span>请输入市</span>
                <select class="NewCity" v-model="NewAddress.city" @click="handleCity">
                <option v-for="item in AllCity" :key="item.id" :value="item.id">
                    {{ item.name }}
                    </option>
                </select>
            </div>
            <div  class="NewRegion">
                <span>请输入区</span>
                <select class="NewRegion" v-model="NewAddress.region" @click="handleRegion">
                <option v-for="item in AllRegion" :key="item.id" :value="item.id">
                    {{ item.name }}
                    </option>
                </select>
            </div>
            <input class="NewDetail" placeholder="请输入详细收货地址" v-model="NewDetail">
            <input class="NewName" placeholder="请输入收货人姓名" v-model="NewName">
            <input class="NewPhone" placeholder="请输入手机号" v-model="NewPhone">
        </form>
</van-dialog>
  </div>
</template>

<script>
import { getAddressList, getProvinceList, AddAddress, editAddress, removeAddress, getDefAddress, setDefAddress } from '@/api/address'
export default {
  name: 'myAddressIndex',
  async created () {
    // 获取收货地址列表
    const res = await getAddressList()

    console.log(res)
    this.AddressList = res.data.list
    // console.log(this.AddressList)
    // 获取省市区
    const res2 = await getProvinceList()
    this.AllProduct = res2.data.list
    // console.log(this.AllProduct)
    // 获取默认地址
    const def = await getDefAddress()
    console.log(def)
    this.defId = def.data.defaultId
  },
  data () {
    return {
      AddressList: [],
      editShow: false,
      addShow: false,
      NewAddress: {
        province_id: '',
        province: '',
        city: '',
        city_id: '',
        region: '',
        region_id: ''
      },
      NewDetail: '',
      NewName: '',
      NewPhone: '',
      AddressId: '',
      AllProduct: {},
      AllCity: {},
      AllRegion: {},
      ProvinceShow: false,
      changeButton: true,
      defId: ''
    }
  },
  methods: {
    async handleChange (id) {
      this.editShow = true
      this.AddressId = id
    },
    handleCity () {
      if (!this.NewAddress.province) {
        return
      }
      this.AllCity = this.AllProduct[this.NewAddress.province].city
    },
    handleRegion () {
      if (!this.NewAddress.city) {
        return
      }
      this.AllRegion = this.AllCity[this.NewAddress.city].region
    },
    async changeAddress () {
      const province = this.AllProduct[this.NewAddress.province].name
      const city = this.AllCity[this.NewAddress.city].name
      const region = this.AllRegion[this.NewAddress.region].name
      const provinceId = this.NewAddress.province
      const cityId = this.NewAddress.city
      const regionId = this.NewAddress.region
      if (this.NewName === '' || this.NewPhone === '' || this.NewDetail === '' || provinceId === '' || cityId === '' || regionId === '') {
        alert('请输入正确的信息')
        return 0
      } else {
        await editAddress(this.AddressId, this.NewName, this.NewPhone, this.NewDetail, [
          { label: province, value: provinceId },
          { label: city, value: cityId },
          { label: region, value: regionId }
        ])
        this.NewAddress.province = ''
        this.NewAddress.city = ''
        this.NewAddress.region = ''
        this.NewDetail = ''
        this.NewName = ''
        this.NewPhone = ''
      }
      const res = await getAddressList()
      this.AddressList = res.data.list
    },
    addAddress () {
      this.addShow = true
    },
    async addAddressDialog () {
      const province = this.AllProduct[this.NewAddress.province].name
      const city = this.AllCity[this.NewAddress.city].name
      const region = this.AllRegion[this.NewAddress.region].name
      const provinceId = this.NewAddress.province
      const cityId = this.NewAddress.city
      const regionId = this.NewAddress.region
      await AddAddress(this.NewName, this.NewPhone, this.NewDetail, [
        {
          value: provinceId,
          label: province
        },
        {
          value: cityId,
          label: city
        },
        {
          value: regionId,
          label: region
        }
      ])
      const res = await getAddressList()
      this.AddressList = res.data.list
      this.NewAddress.province = ''
      this.NewAddress.city = ''
      this.NewAddress.region = ''
      this.NewDetail = ''
      this.NewName = ''
      this.NewPhone = ''
    },
    async removeAddress (id) {
      await removeAddress(id)
      const res = await getAddressList()
      this.AddressList = res.data.list
    },
    async setDefAddress (id) {
      await setDefAddress(id)
      this.defId = id
    }
  },
  watch: {
    'NewAddress.province': {
      deep: true,
      handler (newValue) {
        this.NewAddress.city = ''
        this.NewAddress.region = ''
      }
    },
    'NewAddress.city': {
      deep: true,
      handler (newValue) {
        this.NewAddress.region = ''
      }
    }
  }
}
</script>

<style lang="less" scoped>
    .AddressHead{
        display: flex;
        .headLeft{
            margin-right: auto;
            color: green;
            margin-top: 5px;
            margin-left: 10px;
        }
        .headRight{
            margin-left: auto;
            color: green;
            margin-top: 5px;
            margin-right: 10px;
        }
    }
    .AddressBody{
        display: flex;
        .BodyInfo{
            display: flex;
            flex-direction: column;
            .name{
                margin-left: 10px;
                margin-top: 15px;
            }
            .address{
                margin-left: 10px;
            }
            .bodyChecked{
                margin-left: 10px;
                margin-top: 5px;
            }
        }
        border-bottom: 1px solid gray;
        padding-bottom: 12px;
        .BodyRight{
            margin-top: 20px;
            margin-left: auto;
            margin-right: 15px;
        }
        .BodyRightButton{
            width: 50px;
            border: none;
            margin-left: auto;
            margin-right: 15px;
            margin-top: 20px;
            background-color: rgb(254, 41, 41);
            color: white;
            border-radius: 5px;
        }
    }
    .editForm{
        input{
            width: 90%;
            margin-left: 10px;
            border: none;
            margin-top: 5px;
            margin-bottom: 5px;
            border-bottom: solid 1px gray;
            padding: 3px;
        }
        .NewProvince{
            width: 80%;
            margin-left: 10px;
            margin-right: 10px;
        }
        .NewCity{
            width: 80%;
            margin-left: 10px;
            margin-right: 10px;
        }
        .NewRegion{
            width: 80%;
            margin-left: 10px;
            margin-right: 10px;
        }
    }
</style>
